import {
  IonBackButton,
  IonButton,
  IonButtons,
  IonContent,
  IonHeader,
  IonInput,
  IonItem,
  IonLabel,
  IonPage,
  IonRow,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { useUser } from "hooks/useUser";
import { FormEvent, useState } from "react";
import { useHistory } from "react-router-dom";
import { User } from "types/User";

const SignIn: React.FC = () => {
  const { signIn } = useUser();
  const history = useHistory();

  const [formData, setFormData] = useState<any>({
    email: "",
    password: "",
  });

  const submit = async (event: FormEvent, user: User) => {
    event.preventDefault();
    console.log(user);
    try {
      const response = await signIn(user);
      console.log({ response });
      history.replace("/");
    } catch (error: any) {
      alert(error.message);
    }
  };

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Login</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Login</IonTitle>
          </IonToolbar>
        </IonHeader>

        <form
          className="ion-margin-top ion-margin-bottom"
          onSubmit={(e) => submit(e, formData)}
        >
          <IonItem>
            <IonLabel position="floating">E-mail</IonLabel>
            <IonInput
              type="email"
              name="email"
              value={formData?.email}
              onIonChange={(e) =>
                setFormData({ ...formData, email: e.detail.value! })
              }
              autocomplete="email"
              required
            />
          </IonItem>

          <IonItem>
            <IonLabel position="floating">Senha</IonLabel>
            <IonInput
              type="password"
              value={formData?.password}
              onIonChange={(e) =>
                setFormData({ ...formData, password: e.detail.value! })
              }
              autocomplete="current-password"
              name="password"
              required
            />
          </IonItem>

          <div className="ion-margin-top ion-padding">
            <IonButton type="submit" expand="block" shape="round" size="large">
              Entrar
            </IonButton>
            <IonRow className="ion-justify-content-center ion-margin-top">
              <IonButton
                type="button"
                fill="clear"
                shape="round"
                size="small"
                routerLink="/cadastro"
              >
                Não tenho conta
              </IonButton>
            </IonRow>
          </div>
        </form>
      </IonContent>
    </IonPage>
  );
};

export default SignIn;
